import React, { useState, useEffect } from "react";
import {
  Input,
  Row,
  Col,
  Card,
  Form,
  message,
  Select,
  Button,
  Tooltip,
} from "antd";
import ExamItemApi from "api/exam-item-api";
import { useHistory, Link } from "react-router-dom";
import Flex from "components/shared-components/Flex";
import * as yup from "yup";
import { InfoCircleOutlined } from "@ant-design/icons";

const { Option } = Select;

let schema = yup.object().shape({
  name: yup.string().required("Tn y  khng c  trng."),
  duration: yup
    .number()
    .moreThan(0, "Thi gian lm bi ln hn 0 pht.")
    .max(360, "Thi gian lm bi ti a 360 pht."),
});

const yupSync = {
  async validator({ field }, value) {
    await schema.validateSyncAt(field, { [field]: value });
  },
};

const GeneralField = (props) => {
  const [item, setItem] = useState(null);
  const [submitLoading, setSubmitLoading] = useState(false);
  const [form] = Form.useForm();
  const [isValidForm, setIsValidForm] = useState(true);

  let history = useHistory();

  const validateForm = () => {
    form
      .validateFields(["name"])
      .then((values) => {
        setIsValidForm(true);
      })
      .catch((errorInfo) => {
        setIsValidForm(false);
      });
  };

  useEffect(() => {
    setItem(props.item);
  }, [props.item]);

  const onFinish = () => {
    setSubmitLoading(true);
    item.exam = null;
    new ExamItemApi().save(item).then((response) => {
      setSubmitLoading(false);
      if (item.id === 0) {
        message.success(`To Mn thi thnh cng.`);
        history.push(`/lms/examitems/edit/${response.data.id}`);
      }
      if (item.id > 0) {
        message.success(`Cp nht Mn thi thnh cng.`);
      }
    });
  };

  const handleChange = (event) => {
    validateForm();
    event.persist();
    const currentItem = Object.assign({}, item);
    const targetValue =
      event.target.type === "checkbox"
        ? event.target.checked
        : event.target.value;
    currentItem[event.target.name] = targetValue;
    setItem({ ...currentItem });
    if (props.handleChange) {
      props.handleChange({ ...currentItem });
    }
  };

  return (
    <>
      {item && (
        <>
          <Form
            form={form}
            layout="vertical"
            method="post"
            hideRequiredMark
            autoComplete="off"
            initialValues={item}
          >
            <Row>
              <Col span={24}>
                <Flex
                  className="py-2"
                  mobileFlex={false}
                  justifyContent="between"
                  alignItems="center"
                >
                  <div className="mb-3">&nbsp;</div>
                  <div className="mb-3">
                    <Button
                      type="primary"
                      onClick={() => onFinish()}
                      disabled={!isValidForm}
                      loading={submitLoading}
                    >
                      {item.id === 0 ? "Thm mi" : `Cp nht`}
                    </Button>
                  </div>
                </Flex>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col xs={24} sm={24} md={17}>
                <Card>
                  <Form.Item name="name" label="Tn mn thi" rules={[yupSync]}>
                    <Input
                      name="name"
                      value={item.name}
                      defaultValue={item.name}
                      onChange={(event) => handleChange(event)}
                    />
                  </Form.Item>
                  <Form.Item label="M t">
                    <Input.TextArea
                      name="content"
                      value={item.content}
                      defaultValue={item.content}
                      onChange={(event) => handleChange(event)}
                      rows={4}
                    />
                  </Form.Item>
                </Card>
              </Col>
              <Col xs={24} sm={24} md={7}>
                <Card title="Cu hnh">
                  <Row gutter={16}>
                    <Col xs={24} sm={24} md={24}>
                      <Form.Item label="S ln thi cho php">
                        <Input
                          name="numberOfExamAttemptAllow"
                          value={item.numberOfExamAttemptAllow}
                          defaultValue={item.numberOfExamAttemptAllow}
                          onChange={(event) => handleChange(event)}
                          suffix={
                            <Tooltip title="Mt khu c ti a 6 k t, bao gm t nht 1 k t c bit.">
                              <InfoCircleOutlined
                                style={{ color: "rgba(0,0,0,.45)" }}
                              />
                            </Tooltip>
                          }
                        />
                      </Form.Item>
                    </Col>
                    <Col xs={24} sm={24} md={24}>
                      <Form.Item
                        name="duration"
                        label="Thi gian lm bi (pht)"
                        rules={[yupSync]}
                      >
                        <Input
                          name="duration"
                          value={item.duration}
                          defaultValue={item.duration}
                          suffix={
                            <Tooltip title="Thi gian bm bi thi ln hn 0 v b hn hoc bng 360 pht.">
                              <InfoCircleOutlined
                                style={{ color: "rgba(0,0,0,.45)" }}
                              />
                            </Tooltip>
                          }
                          onChange={(event) => handleChange(event)}
                        />
                      </Form.Item>
                    </Col>
                  </Row>
                  <Row gutter={16}>
                    <Col xs={12} sm={12} md={12}>
                      <Form.Item label="im yu cu t" name="requiredScore">
                        <Input
                          name="requiredScore"
                          value={item.requiredScore}
                          defaultValue={item.requiredScore}
                          onChange={(event) => handleChange(event)}
                          suffix={
                            <Tooltip title="Bi lm ln hn hoc bng im yu cu s c tnh l t.">
                              <InfoCircleOutlined
                                style={{ color: "rgba(0,0,0,.45)" }}
                              />
                            </Tooltip>
                          }
                        />
                      </Form.Item>
                    </Col>
                    <Col xs={12} sm={12} md={12}>
                      <Form.Item label="im tng" name="maxScore">
                        <Input
                          name="maxScore"
                          value={item.maxScore}
                          defaultValue={item.maxScore}
                          onChange={(event) => handleChange(event)}
                          suffix={
                            <Tooltip title="im tng s c chia cho tng cu hi theo h s im ca phn trong Cu trc  thi.">
                              <InfoCircleOutlined
                                style={{ color: "rgba(0,0,0,.45)" }}
                              />
                            </Tooltip>
                          }
                        />
                      </Form.Item>
                    </Col>
                  </Row>
                </Card>
              </Col>
            </Row>
          </Form>
        </>
      )}
    </>
  );
};

export default GeneralField;
